import React, {Component} from 'react';
import '../App.css'
import { Button } from 'react-bootstrap'

class Form extends Component  {

    state = {
        search: '',
    }

    render(){
        return (
            <form onSubmit={this.props.refresh} style= {{  display: "flex", flexDirection: "column", width: "250px"}}> 
                <input type="text" name="itemName" placeholder="search by name" onChange={this.props.updateSearch} /> 
                <button className="buttonAll">Refresh</button>
            </form>
        )
    }
    

}
export default Form