import React from 'react'
import { Link } from 'react-router-dom'
import Search from './Form'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, ButtonToolbar } from 'react-bootstrap'
import Card_shop from './Card_shop'
import Form from 'react-bootstrap/Form'
import { DropdownButton } from 'react-bootstrap'
import MenuItem from 'react-bootstrap/Dropdown'
import Spinner from 'react-bootstrap/Spinner'
import Categorie from './CategoriesBtn'
import PriceFilter from './PriceFilter'
import Banners from './Banners'
import '../App.css'
import Pagination from './Pagination'
import Pagination2 from './Pagination2'

export default class Student extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            items: [],
            id: 's3818180_',
            name: '',
            price: '',
            description: '',
            imageURL: '',
            producer: 's3818180_real_one',
            brand: '',
            productTypeId: '',
            search: '',
            item_by_Price: [],
            priceMax: 150,
            displayColumn: false,
            loading: true,
            loadNumber: 6,

        }
    }

    fetchData() {
        this.setState({ loading: true })
        var url = 'http://13.251.156.195:8080/products'
        fetch(url)
            .then(res => res.json())
            .then(json => this.setState({ items: json, loading: false }))
        console.log(this.state.loading)
    }


    componentDidMount() {
        this.fetchData()
    }

    handleChange(e) {

        var obj = {}
        obj[e.target.name] = e.target.value
        this.setState(obj)
    }

    updateSearch = (event) => {
        this.setState({ search: event.target.value.substr(0, 20) })
        var filteredData = this.state.items.filter(
            (item) => {
                return item.producer == "s3818180_real_one"
            }
        )
        var filteredDataSearch = filteredData.filter(
            (item) => {
                return item.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1
            }
        )
        this.setState({ items: filteredDataSearch })
        if (event.target.value == '') {
            this.fetchData()
        }
    }
    getCategorie = (arg) => {
        var filteredDataCategorie = this.state.items.filter(
            (item) => {
                return item.brand == arg
            }
        )
        this.setState({ items: filteredDataCategorie })
    }

    getAll = async () => {
        this.fetchData()
    }

    getPrice = async (e) => {

        e.preventDefault();
        this.fetchData()
        const lower_price = e.target.elements[0].value
        setTimeout(() => {
            var filteredData = this.state.items.filter(
                (item) => {
                    return item.producer == "s3818180_real_one"
                }
            )

            var filteredDataPrice = filteredData.filter(
                (item) => {
                    return item.price < lower_price
                }
            )
            console.log(filteredDataPrice)
            this.setState({ items: filteredDataPrice })
        }, 1000)
    }

    updateDisplay = () => {
        this.setState({ displayColumn: !this.state.displayColumn })

    }

    paginate = () => {
        alert("I know it doesn't work use the LOAD MORE button")
    }

    loadMore = () => {
        var filteredData = this.state.items.filter(
            (item) => {
                return item.producer == "s3818180_real_one"
            }
        )
        
    var newLoadnumber = this.state.loadNumber += 3
        this.setState({ loadNumber: newLoadnumber })
        if (newLoadnumber > filteredData.length) {
            this.setState({ loadNumber: filteredData.length })
        }
    }


    // <-------------------------------------- CRUD -------------------------------------->

    save(e) {
        e.preventDefault();
        var url = 'http://13.251.156.195:8080/products'
        fetch(url, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({ id: this.state.id, name: this.state.name, price: this.state.price, description: this.state.description, imageURL: this.state.imageURL, producer: this.state.producer, brand: this.state.brand, productTypeId: this.state.productTypeId })
        }).then(res => res.json())
            .then(json => this.fetchData())

    }


    delete(id) {
        var url = 'http://13.251.156.195:8080/products'
        fetch(url + "/" + id, {
            method: 'delete',
        }).then(res => res.json())
            .then(json => this.fetchData())
    }



    // <-------------------------------------- CRUD END-------------------------------------->

    render() {

        // <-------------------------------------- variable declaration-------------------------------------->
        var filteredData = this.state.items.filter(
            (item) => {
                return item.producer == "s3818180_real_one" || item.producer == " s3818180_real_one"
            }
        )
        
        const postsPerPage = this.state.loadNumber;
        const currentPage = this.state.currentPage
        const setCurrentPage = this.state.currentPage

        // Get current posts
        const indexOfLastPost = currentPage * postsPerPage;
        const indexOfFirstPost = indexOfLastPost - postsPerPage;
        const currentPosts = filteredData.slice(indexOfFirstPost, indexOfLastPost);

        var getAllCategories = filteredData.map(
            (item) => {
                return item.brand
            }
        )

        let categorieArray = [...new Set(getAllCategories)];

        var showGrid = this.state.displayColumn
        // <-------------------------------------- CSS -------------------------------------->

        const cardGrid = {
            width: '21rem',
            background: 'radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(35,130,232,1) 0%, rgba(126,224,255,1) 100%)',
            marginTop: "20px"
        }
        const cardColumn = {
            width: '35rem',
            background: 'radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(35,130,232,1) 0%, rgba(126,224,255,1) 100%)',
            marginTop: "20px"
        }
        const items_display_grid = {
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "space-around",
            width: "80%"
        }

        const items_display_column = {
            display: "flex",
            flexDirection: "column",
            flexWrap: "wrap",
            justifyContent: "space-around",
            alignItems: "center",
            width: "80%"
        }

        const link_button = {
            textDecoration: 'none',
            color: 'white'

        }
        return (
            <div>
                <Banners imgbanner="../img/Fortnite_Logo.png" bannerHeight="40%" bannerWidth="40%" />
                <h2 style={{ textAlign: "center", margin: "50px" }} className="fontTitle">dematerialized shop</h2>
                <div >
{/* SIDEBAR */}
                    <div style={{ display: "flex", justifyContent: "space-around" }} className="sidebarContainer">
                        <PriceFilter getPrice={this.getPrice} />
                        <Search refresh={this.fetchData} updateSearch={this.updateSearch} />
                        <div style={{ width: "250px", display: "flex", justifyContent: "center" }} >
                            <DropdownButton title="Categories" >
                                {categorieArray.map(categorie =>
                                    <MenuItem><Categorie getCategorie={() => this.getCategorie(categorie)} Categorie={categorie} /></MenuItem>
                                )}
                                <MenuItem><Categorie getCategorie={() => this.fetchData()} Categorie="All" /></MenuItem>
                            </DropdownButton>
                        </div>
                    </div>
{/* SIDEBAR END*/}
{/* SHOP */}
                    <ButtonToolbar style={{ display: "flex", justifyContent: "center", marginBottom: "40px" }}>
                        <Button onClick={this.updateDisplay} variant="primary">Change display</Button>
                    </ButtonToolbar>
                    {filteredData.length != 0 ? <div className="center">{filteredData.length} items availables : </div> : <div className="center">If there is no data, add an item in the form</div> }
                    <div style={{ display: "flex", justifyContent: "center" }}>
                        {this.state.loading ? <Spinner animation="border" role="status" style={{ marginTop: "10%" }}><span className="sr-only">Loading...</span></Spinner> :                                
                            <div style={showGrid === true ? items_display_column : items_display_grid} >                                
                                {filteredData.slice(0, this.state.loadNumber).map(s =>
                                    <div>
                                        <div>
                                            <Card_shop
                                                cardSize={showGrid === true ? cardColumn : cardGrid}
                                                title={s.name}
                                                price={s.price}
                                                img={s.imageURL}
                                                buttonDelete={this.delete.bind(this, s.id)}
                                                link={<Link to={{
                                                    pathname: `/home/${s.id}`,
                                                    state: {
                                                        item_name: s.name,
                                                        id: s.id,
                                                        type: s.productTypeId,
                                                        rarity: s.brand,
                                                        img: s.imageURL,
                                                        price: s.price,
                                                        description: s.description
                                                    }
                                                }}
                                                    style={link_button}>View Details</Link>}
                                            />
                                            {/* <button onClick={this.delete.bind(this, s.id)}>Delete</button> */}
                                            {/* <input type="image" src="../img/deleteButton.png" onClick={this.delete.bind(this, s.id)} className="deleteButton"/> */}
                                        </div>
                                    </div>
                                )}
                            </div>
                        }
                        
                    </div>
                    <div className="loadMore">
                        <Button onClick={this.loadMore} variant="primary" >Load more</Button>
                    </div>
{/* SHOP END*/}
{/* CRUD */}
                    <div style={{ width: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <Pagination postsPerPage={postsPerPage} totalPosts={filteredData.length} paginate={this.paginate} />
                    </div>
                    <h2 style={{ textAlign: "center", marginTop: "100px" }} className="fontTitle">Add items : </h2>
                    <div style={{ display: "flex" }}>
                        <div style={{ width: "50%", display: "flex", flexDirection: "column", alignItems: "center" }}>
                            <input type="text" id="id" name="id" value={this.state.id} onChange={this.handleChange.bind(this)} placeholder="Items id:" className="CrudForm" />
                            <input type="text" id="name" name="name" value={this.state.name} onChange={this.handleChange.bind(this)} placeholder="Items name: " className="CrudForm" />
                            <input type="number" id="price" name="price" value={this.state.price} onChange={this.handleChange.bind(this)} placeholder="Items Price:" className="CrudForm" />
                            <input type="text" id="description" name="description" value={this.state.description} onChange={this.handleChange.bind(this)} placeholder="Items Description: " className="CrudForm" />
                        </div>
                        <div style={{ width: "50%", display: "flex", flexDirection: "column", alignItems: "center" }}>
                            <input type="text" id="imageURL" name="imageURL" value={this.state.imageURL} onChange={this.handleChange.bind(this)} placeholder="Items imageURL: " className="CrudForm" />
                            <input type="text" id="brand" name="brand" value={this.state.brand} onChange={this.handleChange.bind(this)} placeholder="Items categorie: " className="CrudForm"/>
                            <span className="categorieSpan">add a new categorie will update the categorie filter at the top right</span>
                            <input type="text" id="productTypeId" name="productTypeId" value={this.state.productTypeId} onChange={this.handleChange.bind(this)} placeholder="Items Type: " className="CrudForm" />
                        </div>
                    </div>
{/* CRUD END */}                    
                    <div style={{ display: "flex", justifyContent: "center" }}>
                        <Button variant="primary" onClick={this.save.bind(this)} className="ButtonCrud">Save</Button>
                    </div>

                </div>

            </div>
        )
    }

}

