import React, {  Component } from 'react';
// import React, { useState, useEffect, Component } from 'react';
import { Link } from 'react-router-dom'
import Form from './Form'
import PriceFilter from './PriceFilter'
import Card_shop from './Card_shop'
import Banners from './Banners'
import Categorie from './CategoriesBtn'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, ButtonToolbar } from 'react-bootstrap'
import { DropdownButton } from 'react-bootstrap'
import MenuItem from 'react-bootstrap/Dropdown'
import Spinner from 'react-bootstrap/Spinner'
import Pagination from './Pagination'


class Shop extends Component {
    state = {
        items: [],
        search: '',
        loading: true,
        displayColumn: false,
        btnCategorieActive: false,
        loadNumber: 6,
        isFetch: false,
        currentPage : 1,
        numberItems: 0
    }

    getData = async (e) => {
        this.setState({loading: true})
        const api_call = await fetch('http://www.mocky.io/v2/5de67cbe370000a21d09241e')
        const data = await api_call.json()
        this.setState({ items: data.data, loading: false, numberItems : this.state.items.length})
        
    }

    async componentDidMount() {
        this.getData()
    }

    updateSearch = (event) => {
        console.log(event.target.value)
        this.setState({ search: event.target.value.substr(0, 20) })
        var filteredData = this.state.items.filter(
            (item) => {
                return item.item.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1
            }
        )
        this.setState({ items: filteredData, numberItems: filteredData.length })

        if (event.target.value == '') {
            this.getData()
        }
    }

    refresh = async (e) => {
        this.setState({loading: true})
        let default_data = ''
        e.target.elements[0].value = default_data
        e.preventDefault()
        const api_call = await fetch('http://www.mocky.io/v2/5de67cbe370000a21d09241e')
        const data = await api_call.json()
        this.setState({ items: data.data })
        this.setState({ search: default_data })
        this.setState({loading: false})
    }

    getPrice = async (e) => {

        this.setState({loading: true})
        e.preventDefault();
        this.getData()
        const lower_price = e.target.elements[0].value
        setTimeout(() => {
        
        if (this.state.loading == false) {
            var filteredData = this.state.items.filter(
                (item) => {   
                    console.log(Math.round(item.store.cost * 0.00999) <= lower_price)   
                               
                    return  Math.round(item.store.cost * 0.00999) < lower_price
                } 
            )
            console.log(filteredData.length)
            this.setState({ items: filteredData, numberItems: filteredData.length })
        }
        }, 1000)
        this.setState({loading: false})
    }

    getCategorie = (arg) => {
        this.setState({loading: true})
        console.log(this.componentDidCatch)
        this.getData()
        setTimeout(() => {
            var filteredData = this.state.items.filter(
                (item) => {
                    return item.item.rarity == arg
                }
            )
            this.setState({ items: filteredData, numberItems: filteredData.length })
        }, 1000)
        // this.state.loadNumber = this.state.items.length
    }

    updateDisplay = () => {
        this.setState({ displayColumn: !this.state.displayColumn })
        this.setState({ btnCategorieActive: !this.state.btnCategorieActive })
    }

    loadMore = () => {
        console.log(this.state.items.length)
        var newLoadnumber = this.state.loadNumber += 3
        this.setState({ loadNumber: newLoadnumber })
        if (newLoadnumber > 18){
            this.setState({ loadNumber: this.state.items.length })
        }
    }

    paginate = (pageNumber) => {
        // setCurrentPage([2])
        // pageNumber => setCurrentPage([pageNumber], console.log(pageNumber))
        // const paginate = pageNumber => setCurrentPage([pageNumber])
        // console.log("oui")
        alert("I know it's doesn't work, use load more button ")
    };

    render() {

        //---------- Style ---------- 
        const shop_container = {
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center"

        }

        const btn_categorie_active = {
            backgroundColor: "white",
            color: "blue"
        }

        const btn_categorie = {
            background:" white !important",
            color: "white"
        }
        const items_display_grid = {
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "space-around",
            width: "80%"
        }

        const items_display_column = {
            display: "flex",
            flexDirection: "column",
            flexWrap: "wrap",
            justifyContent: "space-around",
            alignItems: "center",
            width: "80%"
        }

        const item_container = {
            margin: "20px"
        }

        const filter_container = {
            display: "flex",
            justifyContent: "center",
            width: "80%",
        }

        const link_button = {
            textDecoration: 'none',
            color: 'white'

        }

        const cardGrid = {
            width: '21rem',
            background: 'radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(35,130,232,1) 0%, rgba(126,224,255,1) 100%)'
        }
        const cardColumn = {
            width: '35rem',
            background: 'radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(35,130,232,1) 0%, rgba(126,224,255,1) 100%)'
        }
        //---------- Style END ---------- 

        var filteredData = this.state.items
        var showGrid = this.state.displayColumn
        var categorieBtnActive = this.state.btnCategorieActive

        const postsPerPage = this.state.loadNumber;
        const currentPage = this.state.currentPage
        const setCurrentPage = this.state.currentPage

        // Get current posts
        const indexOfLastPost = currentPage * postsPerPage;
        const indexOfFirstPost = indexOfLastPost - postsPerPage;
        const currentPosts = filteredData.slice(indexOfFirstPost, indexOfLastPost);

        //change page 
        return (
            <div style={shop_container}>
                <Banners imgbanner="../img/Fortnite_Logo.png" bannerHeight="40%" bannerWidth="40%" />
                {/* sidebar */}
                <div style={filter_container}>
                    
                    <PriceFilter getPrice={this.getPrice} maxPrice={16} />
                    <Form refresh={this.refresh} updateSearch={this.updateSearch} />
                    <DropdownButton title="Categories">
                        <MenuItem><Categorie getCategorie={() => this.getCategorie("rare")} Categorie="rare" /></MenuItem>
                        <MenuItem><Categorie getCategorie={() => this.getCategorie("uncommon")} Categorie="uncommun" /></MenuItem>
                        <MenuItem><Categorie getCategorie={() => this.getCategorie("epic")} Categorie="epic" /></MenuItem>
                        <MenuItem><Categorie getCategorie={this.getData} Categorie="all" /></MenuItem>
                    </DropdownButton>
                </div>
                <ButtonToolbar>
                    <Button onClick={this.updateDisplay} variant="primary" style={categorieBtnActive === true ? btn_categorie_active : btn_categorie}>Change display</Button>
                </ButtonToolbar>
                {/* sidebar End */}
                {/* Shop  */}
                {this.state.loading ? <Spinner animation="border" role="status" style={{marginTop:"10%"}}><span className="sr-only">Loading...</span></Spinner> : 
                    <div style={showGrid === true ? items_display_column : items_display_grid} >
                        {filteredData.slice(this.state.loadNumberStart, this.state.loadNumber).map((item, index) => {
                            return <div key={item.itemId} style={item_container}>
                                <Card_shop
                                    cardSize={showGrid === true ? cardColumn : cardGrid}
                                    title={item.item.name}
                                    price={item.store.cost}
                                    img={item.item.images.icon}
                                    link={<Link to={{
                                        pathname: `/shop/${item.itemId}`,
                                        state: {
                                            item_name: item.item.name,
                                            id: item.itemId,
                                            type: item.item.type,
                                            rarity: item.item.rarity,
                                            img: item.item.images.icon,
                                            price: item.store.cost,
                                            featured: item.item.images.featured,
                                            description: item.item.description
                                        }
                                    }} style={link_button}>View Details</Link>}
                                />

                            </div>
                        })}
                    </div>
                }
                <p style={{marginTop : "100px"}}>{this.state.items.length} items available</p>
                <Button onClick={this.loadMore} variant="primary" >Load more</Button>
                {/* <div style={{display: "flex", justifyContent:"flex-end", width: "70%", marginTop: "50px"}}>
                    
                    <div style={{    width: "100%", display: "flex", justifyContent: "center", alignItems: "center"}}>
                        <Pagination postsPerPage={postsPerPage} totalPosts={filteredData.length} paginate={this.paginate}/>
                    </div>

                </div> */}
                {/* Shop  */}
            </div>
        );
    }

}

export default Shop;