import React, {Component} from 'react';
import { MDBBtn, MDBBtnGroup, MDBDropdown, MDBDropdownToggle, MDBDropdownMenu, MDBDropdownItem } from "mdbreact";
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';
import '../App.css'
import Tooltip from 'rc-tooltip';



class PriceFilter extends Component{
    state = {
        value: 0,
        value_max: 100
    
    }

    // handleOnChange = (e) => 
    // handleOnChange = (e) => this.setState({value: e.target.value})
    handleOnChange2 = (e) => this.setState({value_max: e.target.value})

 
    render(){
        const price_style={
            display: "flex",
            justifyContent: "space-around",
            alignItems: "center"
        }
        const price_container= {
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
        }

        const container_range={
            width: "250px"
        }
    
        return(
            
            <div style={price_container}>
                <form onSubmit={this.props.getPrice} style={price_style,{display: "flex", flexDirection: "column"}} >
                    <div style={container_range}>
                        
                        <input type="range" name="itemPrice" min={0} max={100} value={this.state.value_max} className="priceRange slider" onChange={this.handleOnChange2} id="myRange"/> 
                    </div>                       
                    <button>Filter</button>                        
                </form>            
                <p>items price between : 0 and : {this.state.value_max} $ </p>
            </div>
  
        )
    }
}

export default PriceFilter