import React, { useState, useEffect } from 'react';
import Banners from './Banners'
import { Link } from 'react-router-dom'
import Accordion from 'react-bootstrap/Accordion'
import { Button, Card } from 'react-bootstrap'


class ItemsDetails extends React.Component {

    state = {
        activeItem: []
    }

    componentDidMount = () => {
        console.log(this.props.location.state)

    }
    displayImg = () => {
        if (this.props.location.state.featured == null) {
            return <img src={this.props.location.state.img} height="auto" width="70%" style={{ objectFit: "cover" }} />
        }
        else {
            return <img src={this.props.location.state.featured} height="auto" width="70%" style={{ objectFit: "cover" }} />
        }
    }

    render() {
        const img_itemsDetails = {
            width: "50%",
            display: "flex",
            justifyContent: "center",
            filter: "drop-shadow(2px 2px 19px rgba(50,69,105,0.81))"
        }
        const ItemsDetails_container = {
            display: "flex",
            fontSize: "25px",
        }
        const details_container = {
           
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column"
        }
        const VBuck_container = {
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
        }

        const cardBody = {
            textAlign: "center",
        }
        const cardHead ={
            fontSize: "25px",
            fontWeight: "bold",
            color: "#237FE8",
        }
        return (

            <div>

                <div>
                    <Banners imgbanner={this.props.location.state.img} bannerHeight="20%" bannerWidth="20%" />
                </div>

                <h1 className="fontTitle" style={{ textAlign: "center", fontSize: "50px" }}>{this.props.location.state.item_name}</h1>


                <div style={ItemsDetails_container}>
                    <div style={img_itemsDetails}>
                        {this.displayImg()}
                    </div>

                    <Accordion defaultActiveKey="0" style={{ width: "40%", marginTop: "75px" }}>
                        <Card>
                            <Card.Header >
                                <Accordion.Toggle as={Button} variant="link" eventKey="0" style={{fontSize:"20px", fontWeight: "bold",color: "#237FE8", textDecoration: "none"}}>
                                    Rarity
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body style={cardBody} className = "fontBody">{this.props.location.state.rarity}</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header >
                                <Accordion.Toggle as={Button} variant="link" eventKey="1" style={{fontSize:"20px", fontWeight: "bold",color: "#237FE8", textDecoration: "none"}}>
                                    Type
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                                <Card.Body style={cardBody} className = "fontBody">{this.props.location.state.type}</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header >
                                <Accordion.Toggle as={Button} variant="link" eventKey="2" style={{fontSize:"20px", fontWeight: "bold",color: "#237FE8", textDecoration: "none"}}>
                                    Description
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="2">
                                <Card.Body style={cardBody} className = "fontBody">{this.props.location.state.description}</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header >
                                <Accordion.Toggle as={Button} variant="link" eventKey="3" style={{fontSize:"20px", fontWeight: "bold",color: "#237FE8", textDecoration: "none"}}>
                                    Price
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="3">
                                <Card.Body style={cardBody} className = "fontBody">
                                    <div style={details_container}>
                                        <div style={VBuck_container}>
                                            <p style={{ margin: "0" }}>{this.props.location.state.price}</p>
                                            <img src="../img/VBuck.png" height="auto" width="15%" />
                                        </div>
                                        <p>({Math.round(this.props.location.state.price * 0.00999)} $) </p>
                                    </div>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                </div>

            </div>


        );
    }
}



export default ItemsDetails;
